    private init_map = () => {

        this.lat = 51.678418
        this.lng = 7.809007
        this.zoom = 14
        this.rotateControl = true
        this.fullscreenControl = true
        this.marker_title = 'London'
        this.marker_label = 'Our Offices'
    }

    public openMobMaps (): void {
         // If it's an iPhone..
         if( (navigator.platform.indexOf('iPhone') != -1)
            || (navigator.platform.indexOf('iPod') != -1)
            || (navigator.platform.indexOf('iPad') != -1))
            window.open(`maps://maps.google.com/maps?daddr=${this.lat},${this.lng}&amp;ll=`);
        else
            window.open(`http://maps.google.com/maps?daddr=${this.lat},${this.lng}&amp;ll=`);    
    }
