import { Component, OnInit } from '@angular/core';

interface PolicyObject {

    article?: string;
}

@Component({
    // selector: '',
    templateUrl: 'policy.component.html'
})

export class PolicyComponent implements OnInit {
    
    public PrivacyArticle: PolicyObject;
    
    constructor() { }

    
    ngOnInit() {
        this.PrivacyArticle = {
            article: `
            Privacy Policy
            
            General
            
                Technical PRB Public Company Ltd respects the privacy of every individual who visits our websites.This Privacy Policy outlines the information Technical PRB Bros may collect and how we may use that information.
            
            Personal Data
            
                When you do provide us with personal data, we may use that information in the following ways, unless stated otherwise: we may store and process that information to better understand your needs and how we can improve our products and services; we (or a fulfilment house or other third party on our behalf in connection with a promotion) may use that information to contact you; and/or we may provide other third parties with aggregate - but not individual - information about visitors to or users of our sites.. We do not, neither now nor in future, intend to sell, rent or market personal data about you to third parties.
            
                Technical PRB will provide to you all information available on your present enquiry, but will not collect any personal - identifiable information about you (e.g. name, address, telephone number or e-mail address, "personal data" through the website) and use it for the above purposes, unless you indicate your consent by signing the box space below.
            
            Additional Information Collected Automatically
            
                In some cases, we may automatically (i.e., not via registration) collect technical information when you connect to our site that is not personally-identifiable. Examples of this type of information include the type of Internet Browser you are using, the type of computer operating system you are using and the domain name of the website from which you linked to our site.
            
            Information Places Automatically on Your Computer - Cookies
            
                When you view one of our websites, we may store some information on your computer. This information will be in the form of a "Cookie" or similar file and can help us in many ways. For example, Cookies allow us to tailor a website to better match your interest and preferences. With most Internet Browser, you can erase Cookies from your computer hard drive, block all Cookies or receive a warming before a Cookie is stored. Please refer to your Browser instructions or help screen to learn more about these functions.
            
            Copyright © Technical PRB Ltd. All rights reserved
            `
        }
    }
}


