import { Component, HostListener, AfterViewInit }        from '@angular/core'
import { Router }           from '@angular/router'
import { MatTooltip }       from '@angular/material'
import { Scrollspy }        from '../../classes'
import { imagesPath, jpg }  from '../../variables'
import { initNavClass } from '../../routines'
import { EventsService }    from '../../services'
import { WindowScrollComponent } from '../../window'

@Component({

    selector    : 'navig',
    styleUrls   : ['nav.component.css'],
    templateUrl  : 'nav.component.html'
})

export class MenuComponent implements AfterViewInit{

    // Window properties, like Scrollspy
    public win: Scrollspy

    public navclass: Object = {}

    public SlideCollection: Array<Object>

    public pageType: boolean

    public pagerouteError: boolean 

    public topnavcollapse: Object

    public navcollapse: boolean

    public call: Object
    
    
    constructor (private router: Router, private eventListener: EventsService) {

        this.call = {
            name: '+447 490 40 6531'
        }
        
        
        this.pageType = !/admin/.test(window.location.pathname)?  true : false

        // initialize WindowRef
        this.win = new Scrollspy()
        
        // init statement
        this.navcollapse = false

        this.navclass = {
            
            Start: '',
            About: '',
            Services: '',
            RecentWorks: '',
            news: '',
            contact: '',
            blog: '',
            action2: '',
            action3: ''
        }
        
        this.topnavcollapse = {
            'top-nav-collapse': false
        }

        this.SlideCollection = [
            {img: imagesPath + 'slide1' + jpg, headText: 'All kinds of plumbing work are undertaken', extraText: 'by specialized and licensed plumbers!'},
            {img: imagesPath + 'slide4' + jpg, headText: 'We Make Plumbing Simple', extraText: 'in the water and heating sector!' },
            {img: imagesPath + 'firs' + jpg, headText: 'Giving Solutions', extraText: 'for your immediate convenience!' }
        ] 

    }

    public ngAfterViewInit(): void {
        //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
        //Add 'implements AfterViewInit' to the class.
        // Window properties, like Scrollspy
        // events broadcast service
        this.eventListener.on('routerLink', a => {

            this.routerLink(a)
            // console.log('a: ', a)
        })

        this.eventListener.on('pageError', a => {

           this.pagerouteError = a;
        });

        /* this.eventListener.on('navBar', a => {
            
            this.call['name'] = a
            // console.log('a: ', a)
        }) */
        
    }

    @HostListener("window:scroll", [])
    onWindowScroll = () => {
        if(this.isBrowser()){
            this.topnavcollapse['top-nav-collapse'] = this.getCurrentScrollTop() > 100 ? true : false
        }
    }
    
    /**
     * Get current Y scroll position
     * @returns {any|((event:any)=>undefined)}
     */
    private getCurrentScrollTop = () => {
        if(typeof window.scrollY != 'undefined'){
            return window.scrollY;
        }

        if(typeof window.pageYOffset != 'undefined'){
            return window.pageYOffset;
        }

        if(typeof document.body.scrollTop != 'undefined'){
            return document.body.scrollTop;
        }

        if(typeof document.documentElement.scrollTop != 'undefined'){
            return document.documentElement.scrollTop;
        }

        return 0;
    };
    /**
     * This check will prevent 'window' logic to be executed
     * while executing the server rendering
     * @returns {boolean}
     */
    private isBrowser = ():boolean => {
        return typeof (window) !== 'undefined';
    };

    private controllHostListener (cntr: boolean) : void {
        
        this.eventListener.broadcast('WindowScroll', cntr) // set host listener to false via broadcast
    }

    public getImageStyle(image: string): Object {

        // carouse image background image and style
        // console.log(image)
        let imagestyle: Object = {
            'background-image': `url(${image})`,
            'background-attachment': 'fixed',
            'min-height':'700px',
            'min-width':'1286px',
            'height':'auto',
            'width':'auto',
            'display': 'block',
            'opacity': 0.812621,
            'background-position': `0px`
        }
        
        return imagestyle
    }

    public routerLink ( RouterPage: string ): void {

        let dropdown = (RouterPage=='blog' || RouterPage=='privacy')

        // close nav collapse
        this.navcollapse = false

        this.router.navigateByUrl('/' + (dropdown? RouterPage : '')) // navigate and scroll to id

        initNavClass(this.navclass, RouterPage, undefined) // set active nav bar
       
        setTimeout(() => this.win.smoothScroll(RouterPage), 100) // set 100 ms
    }


}


