import { Component, OnDestroy }        from '@angular/core'
import { EventsService }    from '../services/index'

@Component({

  template: `
        <div _ngcontent-c9="" class="view intro hm-purple-slight" style="background-image: url(../../img/Photos/404-er.jpg);background-attachment: fixed;min-height: 700px;border-top: 2px solid #e5e5e5;background-position: center;">
            <div _ngcontent-c9="" class="full-bg-img">
                <div _ngcontent-c9="" class="container">
                    <div _ngcontent-c9="" class="d-flex align-items-center d-flex justify-content-center" style="height: 700px">
                        <div _ngcontent-c9="" class="row mt-5">
                            <div _ngcontent-c9="" class="col-md-12 wow fadeIn mb-3">
                                <div _ngcontent-c9="" class="intro-info-content text-center">
                                    <div class="error-template">
                                        <h1 style="color: #e54040;"  class="display-1 white-text mb-2 wow fadeInDown" data-wow-delay="0.3s">
                                            Oops!</h1>
                                        <br>
                                        <div class="mb-3 mt-1 white-text error-details font-bold wow fadeIn" data-wow-delay="0.4s">
                                            Sorry, an error has occured, Requested page not found!
                                        </div>
                                        <div class="error-actions">
                                            <a (click)="goHome()" class="btn btn-primary btn-lg"><span class="glyphicon glyphicon-home"></span>
                                                Take Me Home </a><a (click)="goContact()" class="btn btn-default btn-lg"><span class="glyphicon glyphicon-envelope"></span> Contact Us </a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
          
        <foot></foot>`,
  styles: [`body { background-image: url(data:image/png;base64,);}
.error-template {padding: 40px 15px;text-align: center;
    }
.error-actions {margin-top:15px;margin-bottom:15px;}
.error-actions .btn { margin-right:10px; }
.error-details {
    font-family: "Century Gothic";
    font-size: 2em;
    color: #666;
    text-align: center;
}`]

})

export class PageNotFoundComponent implements OnDestroy {

    constructor(private eS: EventsService) {

        this.eS.broadcast('pageError', true);
    }

    public ngOnDestroy(): void {
        // Called once, before the instance is destroyed.
        // Add 'implements OnDestroy' to the class.
        this.eS.broadcast('pageError', false);
    }

    public goHome() {

        this.eS.broadcast('routerLink', '/')
    }
    public goContact() {

        this.eS.broadcast('routerLink','contact')
    }

}


