import { Component, OnInit, ViewChild } from '@angular/core';
import { EventsService } from '../../services/index';
import { NgForm } from '@angular/forms';
import { EmailsService } from '../../services/index';

interface EmailObject {

    status?: string;
    response?: string;
    color?: string;
    used?: boolean;
    disabled?: boolean;
}

@Component({
    selector: 'foot',
    templateUrl: 'foot.component.html',
    styleUrls: ['foot.component.css'],
})

export class FootComponent implements OnInit {

    // admin Dashboard
    public pageType: boolean;


    @ViewChild('subScribe')
    private subScribe: NgForm;

    public email: Object;
    public emailicon: EmailObject;
    public toolTipError: Object;

    constructor(private eS: EventsService, private emailServ: EmailsService) {}

    ngOnInit(): void {


        this.pageType = !/admin/.test(window.location.pathname) ? true : false;

        this.EmailIconInit();
    }


    private emailStatus() {

        /* this.subScribe.control.get('email').valueChanges.subscribe( a => {
            console.log(a)
        }) */
    }

    /**
     * routerLink
     */
    public routerLink(link: string): void {

        this.eS.broadcast('routerLink', link);
    }

    /**
     * emailStyle
     */
    private emailStyle(s: string) {

        let p: Object = {};

        switch (s) {
            case 'error':
                p = {
                    'border-bottom': '1px solid #f44336',
                    'box-shadow': '0 1px 0 0 #f44336'
                };
                break;
            case 'valid':
                p = {
                    'border-bottom': '1px solid #4285F4',
                    'box-shadow': '0 1px 0 0 #4285F4'
                };
                break;
            default:
                p = {
                    'border-bottom': '1px solid #bdbdbd',
                    'box-shadow': 'none'
                };
                break;
        }

        return p;
    }

    /**
     * emailChange
     */
    public emailChange(s: string): void {

        if (!this.subScribe.valid) {

            this.toolTipError =  this.emailStyle('error');
        } else if (s === 'none') {
            this.toolTipError = this.emailStyle(s);
        } else {
            this.toolTipError = this.emailStyle('valid');
        }

    }

    /**
     * Emal subscribe button
     */
    public subscribe(): void {


        if (this.subScribe.valid) {

            // disable input
            this.emailicon.disabled = !0;

            this.emailServ.subScribe(this.subScribe.controls.email.value).subscribe( res => {

                console.log(res);
                this.setEmailIcon('done', 'green', true);
                setTimeout( () => { this.EmailIconInit(); }, 3000);

            }, error => {

                console.log(error);
                this.setEmailIcon('error_outline', 'red', true);
                setTimeout( () => { this.EmailIconInit(); }, 3000);
            });
        }
    }

    private setEmailIcon(res: string, color: string, used: boolean ): void {

        this.emailicon.used = used;
        this.emailicon.response = res;
        this.emailicon.color = color;
    }

    private EmailIconInit(): void { this.emailicon = { status: '', response: '', color: '', used: false, disabled: false }; }
}




